<template>
  <div id="app">
    <div class="head">
      <div class="container">
        <span>
            <a href="http://ceshi.nainaiwang.com/">
                <img src="/static/img/icon_index.png" alt="">
                耐耐网首页
            </a>
        </span>
        <span v-if="username">
          您好，
            {{username}}
          <!--<a href="#">退出</a>-->
        </span>
        <span v-if="!username">
          您好，欢迎进入耐耐网
          <a href="http://ceshi.nainaiwang.com/user/login/login" style="margin-left: 20px;">请登录</a>
          <a href="http://ceshi.nainaiwang.com/user/login/register" style="margin-left: 10px;">免费注册</a>
        </span>
        <div class="to-right">
          <ul>
            <li><a href="javascript:;" @click="goList()">店铺列表</a></li>
            <li>|</li>
            <li><a href="javascript:;" @click="goStore()">卖家中心</a></li>
            <li>|</li>
            <li><a href="http://ceshi.nainaiwang.com/user/message/usermail">消息中心</a></li>
            <li>|</li>
            <li><a href="http://crm2.qq.com/page/portalpage/wpa.php?uin=4006238086&aty=0&a=0&curl=&ty=1">在线客服</a></li>
            <li>|</li>
            <li>交易时间: 00:00:00--23:30:00</li>
          </ul>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        username: sessionStorage.getItem("nainaiUsername")
      }
    },
    methods: {
      goStore() {
        // 跳转去卖家中心
        window.open('/back/index.html#/jump?sd=' + sessionStorage.getItem("nainaisd"))
      },
      goList() {
        this.goTo('/');
      },

      // 获取用户名
      getUsername() {
        this.Axios.post(this.baseUrl + '/user/selectUsernameBySessionId', {
          sd: sessionStorage.getItem("nainaisd")
        }).then((res) => {
          console.log(res, "sessionId 用户名")
          if (res.data.data.username && res.data.data.username != '') {
            sessionStorage.setItem("nainaiUsername", res.data.data.username);
            this.username = sessionStorage.getItem("nainaiUsername");
          } else {
            this.username = null;
          }
        }).catch((err) => {
          console.log(err, "获取用户名");
        })
      }
    },
    mounted() {
      this.getUsername();
    }
  }
</script>

<style scoped lang="less">
  .head {
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #8b8b8b;
    font-size: 12px;
    border-bottom: 1px solid #e1e1e1;
    a {
      color: #8b8b8b;
      text-decoration: none;
    }
    img {
      position: relative;
      top: 5px;
    }
    span {
      margin-right: 20px;
      a:hover {
        color: #c81623;
      }
    }
    ul {
      list-style: none;
    }
    .to-right {
      float: right;
      li {
        float: left;
        padding: 0 4px;
      }
    }
  }

  .container {
    width: 1190px;
    margin: 0 auto;
  }
</style>



