<template>
  <div>
    <div>
      <v-swiper></v-swiper>
    </div>
    <div class="module" v-if="shopModules.length > 0">
      <div class="container">
        <h2 style="text-align: center">推广</h2>
        <div class="module-warp">
          <div class="module-item" v-for="(item,index) in shopModules">
            <a href="javascript:;">
              <img :src="item.picturePath" :alt="item.name" @click="searchModule(item.id)" style="">
            </a>
          </div>
        </div>
      </div>
    </div>
    <div class="all-goods" style="margin-top: 40px">
      <div class="container" style="overflow: hidden;">
        <v-goods></v-goods>
      </div>
    </div>

  </div>
</template>

<script>
  import Swiper from '@/components/view/swiper'
  import Allgoods from '@/components/view/Allgoods'
  export default {
    name: "home",
    data() {
      return {
        shopModules: []
      }
    },
    methods: {

      //获取店铺id
      getShopId() {
        if(!sessionStorage.getItem("nainaiShopId")){
          let str = window.location.href;
          let res = str.substring(str.length - 32);
          sessionStorage.setItem("nainaiShopId",res);
        }else{

        }
      },

      // 获取推广信息
      getModule() {
        this.Axios.post(this.baseUrl + '/shopmodule/selectShopModuleShopId', {
          shopId: sessionStorage.getItem("nainaiShopId"),
          isBackgroundSystem: 0
        }).then((res) => {
          console.log(res, "mokuai");
          this.shopModules = res.data.data.shopModules;
        }).catch((err) => {
          console.log(err);
        })
      },

      // 点击模块
      searchModule(param) {
        let obj = {
          proName: '',
          moduleId: param,
          classifyId: '',
          navigationId: ''
        }
        sessionStorage.setItem("nainaiSearch",JSON.stringify(obj));
        this.goTo('/goods/' + param);
      },
    },
    components: {
      'v-swiper': Swiper,
      'v-goods': Allgoods,
    },
    mounted() {
      this.getModule();
      this.getShopId();
    }
  }
</script>

<style scoped lang="less">
  .container {
    width: 1190px;
    margin: 0 auto;
  }
  .module {
    .container {
      padding-top: 20px;
      .module-warp {
        overflow: hidden;
        width: 1000px;
        margin: 0 auto;
      }
      h2 {
        margin-bottom: 30px;
      }

      .module-item:first-child {
        float: left;
        width: 550px;
        height: 450px;
        margin-left: 20px;
        text-align: center;
        img {
          width: 500px;
          height: 420px;
        }
      }
      .module-item:nth-child(2), .module-item:nth-child(3) {
        float: left;
        width: 400px;
        height: 225px;
        text-align: center;
        img {
          width: 350px;
          height: 200px;
        }
      }
    }
  }

  .all-goods {
    margin-bottom: 20px;
  }
</style>



