<template>
  <el-carousel :interval="5000" arrow="always" height="650px">
    <el-carousel-item v-for="(item,index) in bannerList" :key="index" >
      <a href="javascript:;">
        <img :src="item.picturePath" :alt="item.name" @click="goDetail(item.offerId, item.productsId)">
      </a>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
    export default {
        name: "swiper",
      data() {
          return {
            bannerList: [1,2,3]
          }
      },
      methods: {

        // 点击商品
        goDetail(id,pid) {
          window.open("http://ceshi.nainaiwang.com/offers/offerdetails/id/" + id + "/pid/" + pid);
        },

          // 获取 轮播
        getBanner() {
          this.Axios.post(this.baseUrl + '/shopcarousel/selectShopCarouselShopId',{
            shopId: sessionStorage.getItem("nainaiShopId"),
            isBackgroundSystem: 0
          }).then((res) => {
            console.log(res,"轮播");
            this.bannerList = res.data.data.shopCarousels;
          }).catch((err) => {
            console.log(err);
          })
        }

      },
      mounted() {
        this.getBanner();
      }
    }
</script>

<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
img{
  width: 100%;
}
</style>



