<template>
    <div>
      <div class="search">
        <div class="container clear">
          <div class="logo">
            <a href="http://ceshi.nainaiwang.com/ ">
              <img src="/static/img/logo.png" alt="">
            </a>
          </div>
          <div class="search-box">
            <input type="text" v-model="searchWords">
            <button @click="search()">搜索</button>
          </div>
        </div>
      </div>
      <div class="nn-nav">
        <div class="container">
          <div id="index_nav">
            <div class="page_width">
              <div class="all_steel" @click="getAllList()"><img class="steel_img" src="/static/img/icon_daoha.png">所有店铺</div>
              <ul class="nav">
                <li><a href="https://www.nainaiwang.com/index/index">首页</a></li>
                <li><a href="https://www.nainaiwang.com/offers/offerlist" target="_blank">交易中心</a></li>
                <li><a href="https://www.nainaiwang.com/bid/tenderlist" target="_blank">招投标</a></li>
                <li class="current"><a href="javascript:;">店铺</a></li>
                <li><a href="https://www.nainaiwang.com/index/storage" target="_blank">仓储专区</a></li>
                <li><a href="https://www.nainaiwang.com/index/found" target="_blank">帮我找</a></li>
                <li><a href="http://info.nainaiwang.com" target="_blank">耐耐资讯</a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div class="main">
        <div class="container">
          <div class="store-list clear" v-for="(item,index) in list">
            <div class="left" @click="goToStore(index)">
              <img :src="item.shopInfos.logo" class="store-logo" alt="">
              <div class="store-msg">
                <h4 class="store-name">{{item.shopInfos.name}}</h4>
                <p class="store-owner"><span v-for="item1 in item.shopInfos.operatingAddress" style="margin-right: 5px">{{item1}}</span></p>
                <p>主营：{{item.shopInfos.mainProject}}</p>
              </div>
            </div>
            <div class="right">
              <span v-for="(subItem,index) in item.shopSpreads">
                <img :src="subItem.picturePath" :alt="item.name" @click="goGoods(subItem.offerId,subItem.productsId)">
              </span>
            </div>
          </div>
          <div style="width: 400px; margin: 0 auto; margin-top: 20px">
            <el-pagination
              :current-page.sync="currentPage"
              :page-size="10"
              layout="prev, pager, next, jumper"
              :total="count"
              @current-change="handleCurrentChange"
            >
            </el-pagination>
          </div>

        </div>
      </div>
      <v-footer></v-footer>
    </div>
</template>

<script>
  import Footer from '@/components/view/Footer'


    export default {
        name: "search",
        data() {
          return {
            list: [],
            searchWords: "",
            currentPage: 1,
            count: null
          }
        },
      methods: {

          // 获取店铺列表
        getList (pageNum, pageSize, keywords) {
          this.Axios.post(this.baseUrl + "/shopinfo/selectShopnfoAndSpreadPage",{
            pageNum: pageNum,
            pageSize: pageSize,
            name: keywords
          })
            .then((res) => {
              console.log(res,"list")
              this.count = res.data.data.count;
              this.list = res.data.data.selectShopnfoAndSpread;
              this.list.forEach((item,index) => {
                item.shopInfos.operatingAddress = JSON.parse(item.shopInfos.operatingAddress);
              })
            }).catch((err) => {
              console.log(err)
          })
        },

        // 当前页改变时
        handleCurrentChange(val) {
          this.getList(val,10,this.searchWords);
        },

        search() {
          this.getList(1,10,this.searchWords);
        },

        //点击店铺
        goToStore(n) {
          sessionStorage.setItem("nainaiShopId",this.list[n].shopInfos.id);
          this.Axios.put(this.baseUrl + "/shopbrowse/updateShopBrowseIdSelective",{
            shopId: this.list[n].shopInfos.id
          }).then((res) => {
            console.log(res,"liulanliang");
          }).catch((err) => {
            console.log(err);
          })
          this.goTo('/home/' + this.list[n].shopInfos.id);
        },

        //点击所有店铺
        getAllList() {
          this.getList(1,10,"");
          this.searchWords = "";
        },

        // 去商品详情页
        goGoods(offerId,productsId){
          window.open("http://ceshi.nainaiwang.com/offers/offerdetails/id/" + offerId + "/pid/" + productsId);
        }
      },
      components: {
        'v-footer' : Footer
      },
      mounted() {
          this.getList(1,10,"");
      }
    }
</script>

