<template>
  <div style="margin-top: 20px; padding-top: 30px; overflow: hidden">
    <div class="container">
      <div class="goods-box" v-for="(item,index) in goodsList" @click="goDetail(item.offer_id,item.product_id)">
        <img :src="'http://ceshi.nainaiwang.com/user/' + item.img.substring(0,item.img.length - 5)" alt="">
        <p class="goods-name">{{item.product_name}}</p>
        <p class="goods-price">￥ <span>{{item.price}}</span>元/{{item.unit}}</p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "goods",
      data() {
          return{
            goodsList:[],
            searchObj: {}
          }
      },
      methods:{
        //获取商品
        getGoods() {
          this.searchObj = JSON.parse(sessionStorage.getItem('nainaiSearch'));
          this.searchObj.shopId = sessionStorage.getItem("nainaiShopId");
          this.Axios.post(this.baseUrl + '/productoffer/selectProductOfferListingByShopId',this.searchObj)
            .then((res) => {
              this.goodsList = res.data.data.productOffers;
          }).catch((err) => {
            console.log(err);
          })
        },

        // 点击商品
        goDetail(id,pid) {
          window.open("http://ceshi.nainaiwang.com/offers/offerdetails/id/" + id + "/pid/" + pid);
        }
      },
      watch: {
        '$route' (to, from){
          this.getGoods();
        }
      },
      mounted() {
        this.getGoods();
      }
    }
</script>

<style scoped lang="less">
  .container{
    width: 1190px;
    margin: 0 auto;
  }
  .goods-box{
    float: left;
    width: 236px;
    text-align: center;
    box-sizing: border-box;
    padding: 10px 20px;
    margin-bottom: 15px;
    cursor: pointer;
    img{
      width: 150px;
      height: 150px;
    }
    .goods-name{
      font-size: 14px;
      margin-top: 5px;
      height: 20px;
      line-height: 20px;
    }
    .goods-price{
      color: #c81623;
      font-weight: 600;
      font-size: 14px;
      margin-top: 5px;
    }
  }
</style>



