import Vue from 'vue'
import Router from 'vue-router'
import Search from '@/components/Search'
import StoreIndex from '@/components/storeIndex'
import Goods from '@/components/view/goods'
import Home from '@/components/view/home'
import Notice from '@/components/view/notice-detail'
import News from '@/components/view/news-detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Search',
      component: Search
    },
    {
      path: '/storeIndex',
      name: 'StoreIndex',
      component: StoreIndex,
      children: [
        {path: '/home/:id',component: Home},
        {path: '/goods/:id',component: Goods},
        {path: '/notice',component: Notice},
        {path: '/news/:id',component: News}
      ]
    }
  ]
})



