<template>
  <div>
    <v-header></v-header>
    <router-view style="margin-bottom: 50px"></router-view>
    <v-footer></v-footer>
    <div id="fixed-div">
      <div class="service">
        <img src="/static/img/service.png" alt="">
        <div class="service-list">
          <p class="service-list-title">客服列表</p>
          <ul>
            <li v-for="(item,index) in serviceList" >
              <a target="_blank" :href="item.qq">
                {{item.nickname}}
                <img src="/static/img/linkUs.png" alt="">
                <span style="font-size: 13px">和我联系</span>
              </a>

            </li>
          </ul>
        </div>
      </div>
      <div class="toTop" v-if="isShowToTop" @click="goTop">
        <img src="/static/img/gototop.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>

  import Footer from '@/components/view/Footer'
  import Header from '@/components/view/Header'

  export default {
    name: "store-index",
    data() {
      return {
        searchObj: {},
        serviceList: [],
        isShowToTop: false
      }
    },
    methods: {



      // 获取客服列表
      getService() {
        this.Axios.post(this.baseUrl + '/shopservice/selectShopServiceShopId',{
          shopId: sessionStorage.getItem("nainaiShopId")
        }).then((res) => {
          console.log(res,"客服")
          this.serviceList = res.data.data.shopServices;
          this.serviceList.forEach((item,index) => {
            item.qq = "tencent://message/?uin=" + item.qqNo + "&Site=&Menu=yes";
          })
        }).catch((err) => {
          console.log(err,"客服列表获取失败");
        })
      },

      // 监听页面滚动
      listenerScroll() {
        let scrollTop =  window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let windowHeight = window.outerHeight;
        if(scrollTop >= windowHeight){
          this.isShowToTop = true;
        }else{
          this.isShowToTop = false;
        }
      },
      goTop() {
        if(document.documentElement.scrollTop == 0){
          document.body.scrollTop = 0;
        }else{
          document.documentElement.scrollTop = 0;
        }
      }
    },
    components: {
      'v-footer': Footer,
      'v-header': Header
    },
    mounted() {
      this.getService();
      window.addEventListener("scroll",this.listenerScroll)
    }
  }
</script>

<style scoped lang="less">
  .container {
    width: 1190px;
    margin: 0 auto;
  }
  #fixed-div{
    position: fixed;
    bottom: 100px;
    right: 60px;
    width: 48px;
    opacity: .4;
    transition: all ease 1s;
    background-color: rgba(255,255,255,.8);
    border: 1px solid #cccccc;
    z-index: 2;
    text-align: center;
    .service:hover{
      .service-list{
        display: block;
      }
    }
    .service{
      height: 50px;
      border-bottom: 1px solid #ccc;
      position: relative;
      img{
        width: 40px;
        margin-top: 5px;
      }
      .service-list{
        display: none;
        width: 180px;
        position: absolute;
        right: 48px;
        bottom: 0;
        background-color: #fff;
        text-align: left;
        padding: 10px;
        border: 1px solid #cccccc;
        font-size: 13px;
        .service-list-title{
          font-size: 14px;
          font-weight: 600;
        }
        ul{
          li{
            padding-left: 20px;
            img{
              width: 22px;
              position: relative;
              top: 4px;
              margin-left: 20px;
            }
          }
        }
      }
    }
    .toTop{
      height: 50px;
      img{
        width: 40px;
        margin-top: 5px;
      }
    }
  }
  #fixed-div:hover{
    opacity: 1;
  }
</style>



