<template>
  <div>
    <div class="search">
      <div class="container clear">
        <div class="logo">
          <a href="http://ceshi.nainaiwang.com/">
            <img src="/static/img/logo.png" alt="">
          </a>
        </div>
        <h3 id="store-name" @click="goTo('/home/' + id)"><a href="javascript:;">{{shopInfo.name}}</a></h3>
        <div class="search-box">
          <input type="text" v-model="searchWords">
          <button @click="search()">搜本店</button>
        </div>

        <div class="news">
          <ul id="con1" ref="con1" :class="{anim:animate==true}">
            <li v-for='item in newsList'>
              <a href="javascript:;" @click="goTo('/news/' + item.id)">{{item.name}}</a>
            </li>
            <li v-if="newsList.length == 0">没有资讯信息</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="nav">
      <div class="container">
        <ul class="menu">
          <li class="all-enter" style="padding: 0; position: relative;">
            <a href="javascript:;" style="display: inline-block; padding: 0 50px;" @click="searchAll()">全部商品</a>
            <ul class="sub-menu">
              <li v-for="(item,index) in navList">
                <a href="javascript:;">{{item.shopNavigationParents.name}}</a>
                <ul class="third-menu">
                  <li v-for="(item1,index1) in item.shopNavigationSons" @click="searchNav(item1.id)">
                    <a href="javascript:;">{{item1.name}}</a>
                  </li>
                </ul>
              </li>
            </ul>
          </li>
          <li v-for="(item,index) in classifys" @click="searchClassify(item.id)">
            <a href="javascript:;">{{item.name}}</a>
          </li>
          <li @click="goTo('/notice')">
            <a href="javascript:;">店铺公告 >></a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "nn-head",
    data() {
      return {
        classifys: [],
        navList: [],
        shopInfo: {},
        searchWords: '',
        animate: false,
        timer: 500,
        newsList: [],
        index: 1,
        interId: null,
        id: sessionStorage.getItem("nainaiShopId")
      }
    },
    methods:  {
      // 滚动
      scroll() {
        let con1 = this.$refs.con1;
        con1.style.marginTop= "-35px";
        this.animate=!this.animate;
        setTimeout(() => {
          this.newsList.push(this.newsList[0]);
          this.newsList.shift();
          con1.style.marginTop='0px';
          this.animate=!this.animate;
        },900)

      },
      // 获取资讯
      getNews() {
        this.Axios.post(this.baseUrl + '/shopnews/selectShopNewsShopIdPage',{
          shopId: sessionStorage.getItem("nainaiShopId"),
          isBackgroundSystem: 0,
          pageNum: 1,
          pageSize: 0
        }).then((res) => {
          console.log(res,"资讯");
          this.newsList = res.data.data.shopNewss;
          if(res.data.data.count > 1){
            this.interId = setInterval(this.scroll,2500)
          }else{

          }
        }).catch((err) => {
          console.log(err);
        })
      },

      // 获取分类
      getClassify() {
        this.Axios.post(this.baseUrl + '/shopclassify/selectShopClassifyShopId',{
          shopId: sessionStorage.getItem("nainaiShopId"),
          isBackgroundSystem: 0
        }).then((res) => {
          console.log(res,"分类");
          this.classifys = res.data.data.shopClassifys;
        }).catch((err) => {
          console.log(err);
        })
      },

      // 获取导航
      getNav() {
        this.Axios.post(this.baseUrl + '/shopnavigation/selectShopNavigationShopId',{
          shopId: sessionStorage.getItem("nainaiShopId"),
          isBackgroundSystem: 0
        }).then((res) => {
          console.log(res,"daohang");
          this.navList = res.data.data;
        }).catch((err) => {
          console.log(err);
        })
      },

      //获取店铺基本信息
      getInfo() {
        this.Axios.post(this.baseUrl + '/shopinfo/selectShopInfoId',{
          id: sessionStorage.getItem("nainaiShopId")
        }).then((res) => {
          console.log(res,"信息");
          this.shopInfo = res.data.data.shopInfo;
        }).catch((err) => {
          console.log(err);
        })
      },

      // 点击搜索按钮
      search() {
        let obj = {
          proName: this.searchWords,
          moduleId: '',
          classifyId: '',
          navigationId: ''
        }
        sessionStorage.setItem("nainaiSearch",JSON.stringify(obj));
        this.goTo('/goods/' + Math.random() * 10);
      },

      // 点击分类
      searchClassify(param) {
        let obj = {
          proName: '',
          moduleId: '',
          classifyId: param,
          navigationId: ''
        }
        sessionStorage.setItem("nainaiSearch",JSON.stringify(obj));
        this.goTo('/goods/' + param);
      },

      // 点击导航
      searchNav(param) {
        let obj = {
          proName: '',
          moduleId: '',
          classifyId: '',
          navigationId: param
        }
        sessionStorage.setItem("nainaiSearch",JSON.stringify(obj));
        this.goTo('/goods/' + param);
      },

      // 点击所有商品
      searchAll() {
        let obj = {
          proName: '',
          moduleId: '',
          classifyId: '',
          navigationId: ''
        }
        sessionStorage.setItem("nainaiSearch",JSON.stringify(obj));
        this.goTo('/goods/all');
      }

    },

    mounted() {
      this.getClassify();
      this.getNav();
      this.getInfo();
      this.getNews();
    },

    beforeDestroy(){
      clearInterval(this.interId);
    },

    beforeDestroy(){
      clearInterval(this.interId);
    }
  }
</script>

<style scoped lang="less">
  .search,
  .main {
    width: 100%;
    min-width: 1190px;
    padding-top: 20px;
  }

  .clear:after {
    content: '';
    display: block;
    overflow: hidden;
    clear: both;
  }

  .container {
    width: 1190px;
    margin: 0 auto;
    /*height: 100%;*/
  }

  .logo {
    float: left;
  }

  .search-box {
    height: 35px;
    float: left;
    margin-top: 15px;
    margin-left: 110px;
    input {
      float: left;
      width: 370px;
      height: 31px;
      border: 2px solid #c81623;
      outline: none;
      padding-left: 5px;
      font-size: 14px;
    }
    button {
      float: left;
      width: 91px;
      height: 100%;
      border: none;
      outline: none;
      background-color: #c81623;
      color: #fff;
      font-size: 16px;
      font-weight: 500;
    }
  }

  #store-name {
    float: left;
    line-height: 50px;
    margin-left: 30px;
    font-size: 16px;
  }

  .nav {
    width: 100%;
    height: 34px;
    background-color: #c81623;
    margin-top: 20px;
    position: relative;
    .all-enter:hover {
      .sub-menu {
        display: block;
      }
    }
    .menu:after{
      content: '';
      display: block;
      overflow: hidden;
      clear: both;
    }
    .menu {
      li:hover {
        background-color: #c81623-30;
      }
      li{
        cursor: pointer;
      }
    }
    .sub-menu {
      display: none;
      position: absolute;
      top: 34px;
      left: 0;
      width: 150px;
      z-index: 99;
      background-color: #fff;
      border: 1px solid #c81623;
      font-size: 13px;
      li {
        position: relative;
        float: none;
        padding: 0;
        a {
          color: #000;
        }
      }
      li:hover {
        background-color: #c81623;
        .third-menu {
          display: block;
        }
        a {
          color: #fff;
        }
      }

      .third-menu {
        display: none;
        position: absolute;
        top: 0;
        left: 150px;
        z-index: 999;
        background-color: #c81623;
        li {
          width: 100px;
          padding: 0;
        }
        li:hover {
          background-color: #c81623-30;
          a {
            color: #fff;
          }
        }
      }
    }
    ul {
      line-height: 33px;
      li {
        text-align: center;
        float: left;
        padding: 0 40px;
        a {
          color: #f5f5f5;
          font-size: 13px;
        }
      }
    }
  }
  .news{
    width: 250px;
    height: 35px;
    margin-left: 50px;
    border: 1px solid #ccc;
    font-size: 13px;
    float: left;
    margin-top: 15px;
    overflow: hidden;
    transition: all 0.5s;
    ul{
      li{
        padding-left: 10px;
        line-height: 35px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .anim{
      transition: all ease .8s;
    }
  }
</style>



