<template>
  <div v-if="goodsList.length > 0">
    <h2 style="text-align: center;">所有商品</h2>
    <div style="margin-top: 20px;">
      <div class="goods-box" v-for="(item,index) in goodsList" @click="goDetail(item.offer_id,item.product_id)">
        <img :src="'http://ceshi.nainaiwang.com/user/' + item.img.substring(0,item.img.length - 5)" alt="">
        <p class="goods-name">{{item.product_name}}</p>
        <p class="goods-price">￥ <span>{{item.price}}</span>元/{{item.unit}}</p>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        name: "allgoods",
      data() {
          return {
            goodsList:[]
          }
      },
      methods: {

        // 点击商品
        goDetail(id,pid) {
          window.open("http://ceshi.nainaiwang.com/offers/offerdetails/id/" + id + "/pid/" + pid);
        },

          // 获取所有商品
        getAllgoods() {
          this.Axios.post(this.baseUrl + '/productoffer/selectProductOfferListingByShopId',{
            shopId: sessionStorage.getItem("nainaiShopId"),
            proName: '',
            moduleId: '',
            classifyId: '',
            navigationId: ''
          })
            .then((res) => {
              console.log(res,"所有商品");
              this.goodsList = res.data.data.productOffers;
            }).catch((err) => {
            console.log(err);
          })
        }
      },
      mounted() {
        this.getAllgoods();
      }
    }
</script>

<style scoped lang="less">
.goods-box{
  border: 1px solid transparent;
  float: left;
  width: 236px;
  text-align: center;
  box-sizing: border-box;
  padding: 10px 20px;
  margin-bottom: 15px;
  cursor: pointer;
  img{
    width: 150px;
    height: 150px;
  }
  .goods-name{
    font-size: 14px;
    margin-top: 5px;
  }
  .goods-price{
    color: #c81623;
    font-weight: 600;
    font-size: 14px;
    margin-top: 5px;
  }
}
</style>



