// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'


Vue.use(ElementUI);
Vue.config.productionTip = false;
Vue.prototype.Axios = Axios;
Vue.prototype.goTo = function(address){
  this.$router.push(address);
}
// Vue.prototype.baseUrl = "http://172.16.8.107:8081";
Vue.prototype.baseUrl = "";

// 从地址栏获取sessionId并进行判断
function getSessionId() {
  let address = window.location.href;
  let index = address.indexOf("sd");
  if(address.length - index > 3 && index != -1){
    let str = address.substring(index + 3);
    sessionStorage.setItem("nainaisd",str);
  }else{

  }
}


getSessionId();
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



