<template>
    <div class="container">
      <div v-if="dataObj.name">
        <h2 style="margin-top: 40px; text-align: center">{{dataObj.name}}</h2>
        <p class="time">发布时间：{{timeStr}}</p>
        <div class="ql-container ql-snow">
          <div class="ql-editor">
            <div v-html="dataObj.content"></div>
          </div>
        </div>
      </div>
      <div v-if="!dataObj.name" style="text-align: center; margin-top: 40px;">店铺没有公告信息！</div>

    </div>
</template>

<script>
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
    export default {
        name: "notice-detail",
        data() {
          return {
            dataObj: {},
            timeStr:''
          }
        },
      methods: {
        // 获取公告
        getNotice() {
          this.Axios.post(this.baseUrl + '/shopnotice/selectShopNoticeShopIdOne',{
            shopId: sessionStorage.getItem("nainaiShopId")
          }).then((res) => {
            console.log(res,"gonggao");
            this.dataObj = res.data.data.shopNotice;
            let str = res.data.data.shopNotice.createTime;
            let obj = new Date(str);
            let year = obj.getFullYear();
            let month = obj.getMonth() + 1;
            let date = obj.getDate();
            let hours = obj.getHours();
            let minute = obj.getMinutes();
            let seconds = obj.getSeconds();
            if(month < 10) {
              month = "0" + month;
            }
            if(date < 10) {
              date = "0" + date;
            }
            if(hours < 10) {
              hours = "0" + hours;
            }
            if(minute < 10) {
              minute = "0" + minute;
            }
            if(seconds < 10) {
              seconds = "0" + seconds;
            }
            this.timeStr = year + "-" + month + "-" + date + " " + hours + ":" + minute + ":" + seconds;
          }).catch((err) => {
            console.log(err);
          })
        }
      },
      mounted() {
        this.getNotice();
      }
    }
</script>

<style scoped>
.container{
  width: 1190px;
  margin: 0 auto;
}
.ql-container.ql-snow{
  border: none;
}
  .time{
    font-size: 14px;
    text-align: right;
    margin-bottom: 20px;
  }
</style>



